.header {
  background-color: cadetblue;
}
.navbar {
  margin: 0px auto;
  width: 88%;
  padding: 20px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.nav-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}
.nav-item {
  list-style: none;
}
.nav-item a {
  text-decoration: none;
  color: black;
}
.nav-item a:hover {
  text-decoration: underline;
  color: rgb(32, 31, 31);
}
